body {
  font-family: Arial, sans-serif;
  background-color: #e8e8e8;
  margin: 0;
  padding: 0;
  color: #ea7013;
}

.content {
  flex: 1;
  margin-left: 250px; /* Ajuste selon la largeur de la sidebar */
  padding: 20px;
}

.app-header {
  background-color: #175ae1; /* Couleur de fond sombre */
  display: flex;
  align-items: center; /* Aligne verticalement le logo et le texte */
  justify-content: space-between; /* Espace entre le logo et le texte */
  padding: 10px;
  height: 150px; 
}



.app-header h1 {
  color: #ec7825; /* Couleur du texte GesApp */
  margin: 0;
  font-size: 2.5rem;
}

.search-bar {
  margin-top: 10px;
  width: 98%;
}

.search-bar input {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.container {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h2 {
  color: #007bff;
  margin-bottom: 20px;
}

form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
}

input, textarea {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

button {
  background-color: #ea6c13;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #ac7412;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

li button {
  margin-left: 5px;
}
