.sidebar {
    position: fixed;
    top: 0;
    left: 0;
    height: 98%;
    width: 230px;
    background-color: #be6012;
    display: flex;
    flex-direction: column;
    padding: 20px;
    justify-content: space-between;
  }
  
  .sidebar h2 {
    text-align: center;
  }
  
  .sidebar ul {
    list-style-type: none;
    padding: 0;
  }
  
  .sidebar ul li {
    margin: 15px 0;
  }
  
  .sidebar ul li a {
    text-decoration: none;
    color: #333;
    font-size: 18px;
  }

  .logo {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .logo img {
    width: 100px; 
    height: auto;
  }

  .sidebar ul li a svg {
    margin-right: 10px;
  }
  
  .sidebar-footer {
    margin-top: 20px;
    text-align: center;
    margin-top: auto;
  }
  
  .settings-btn, .logout-btn {
    width: 100%;
    padding: 10px;
    margin-top: 10px;
    border: none;
    background-color: #ff6347;
    color: white;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .settings-btn:hover, .logout-btn:hover {
    background-color: #ff4500;
  }